import { AppBar, Button, Divider, Drawer, IconButton, List, ListItem, ListItemText, ListSubheader, makeStyles, Toolbar, Typography } from "@material-ui/core";
import { blueGrey, teal } from "@material-ui/core/colors";
import { ArrowBack } from "@material-ui/icons";
import { useHistory } from "react-router";
import MenuIcon from '@material-ui/icons/Menu';
import { useState } from "react";

let useStyles = makeStyles({
    alignRight: {
        marginLeft: "auto",
    }
})

let Navbar = () => {
    // let classes = useStyles()
    let history = useHistory()

    let useStyles = makeStyles((theme) => ({
        mrLeft: {
            marginLeft: "auto",
        },
        headers: {
            [theme.breakpoints.down('sm')]: {
                display: 'none'
            }
        },
        headersInv: {
            [theme.breakpoints.up('md')]: {
                display: 'none'
            }
        },
        menu: {
            width: '200px'
        }

    }))
    let classes = useStyles();

    let [stDrawer, setDrawer] = useState(false);


    return (
        <AppBar color={'primary'} position="static">
            <Toolbar>

                {/* <IconButton onClick={() => history.goBack()}>
                    <ArrowBack></ArrowBack>
                </IconButton> */}

                <Typography variant='h6' onClick={() => history.push('/')} >Doctors Search</Typography >
                {/* <Button className={classes.alignRight} variant='contained' color='secondary'>Hindi</Button> */}

                <Button className={` ${classes.headers}  ${classes.mrLeft}`}>
                    <Typography variant='p' color='textSecondary' onClick={() => history.push('/')}>Home</Typography>
                </Button>

                <Button className={classes.headers}>
                    <Typography variant='p' color='textSecondary'>About Us</Typography>
                </Button>



                <IconButton className={`${classes.headersInv} ${classes.mrLeft}`} onClick={() => setDrawer(true)}>
                    <MenuIcon></MenuIcon>
                </IconButton>

                <Drawer anchor={'right'} open={stDrawer} onClose={() => setDrawer(!stDrawer)}>
                    <List className={classes.menu}>
                        <ListItem>
                            <Typography color={'textSecondary'} variant={'h6'}>
                                Project Doctors
                            </Typography>
                        </ListItem>
                        <ListItem button onClick={() => history.push('/')}>
                            <ListItemText primary="Home"></ListItemText>
                        </ListItem>

                        <Divider variant='middle'></Divider>

                        <ListItem button>
                            <ListItemText primary="About Us"></ListItemText>
                        </ListItem>
                        <Divider variant='middle'></Divider>
                    </List>
                </Drawer>

            </Toolbar>
        </AppBar >
    )
}

export default Navbar;